.backdrop {
  z-index: 100;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  transition: opacity $main-transition;
  &.visually-hidden {
    opacity: 0;
    pointer-events: none;
  }

  &.visually-hidden .modal {
    transform: translate(-50%, -50%) scale(0.9);
  }
}

