.modal {
  width: calc(100% - 30px);
  min-height: 500px;
  background-color: #ffffff;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(0) scale(1);
  transition: transform 500ms;
  padding: 40px;
  font-size: 5px;
  @media screen and (min-width: 480px) {
    width: 450px;
  }
  @media screen and (min-width: 1200px) {
    width: 528px;
  }
}

.modal__title {
  font-size: 18px;
  font-weight: 700;
  line-height: 1.3;
  color: var(--accent-color);
  text-align: center;
  margin-bottom: 20px;
}

.modal-open {
  overflow: hidden;
}

