:root {
  --header-text-color: #fff;
  --first-btn-color: #fff;
  --second-btn-color: #303030;
  --first-title-color: #fff;
  --second-title-color: #303030;
  --first-text-color: #303030;
  --accent-color: #ff6c00;
  --second-sect-bckgrnd: #f7f7f7;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  width: 100%;
