  display: block;
}

button {
  cursor: pointer;
}

body {
  font-family: Open Sans, sans-serif;
}

.list {
  list-style: none;
}

a {
  text-decoration: none;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.soc-list, .pre-title {
  align-items: center;
  display: flex;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.pre-title {
  text-transform: uppercase;
  letter-spacing: .1em;
  font-size: 11px;
  font-weight: 600;
  line-height: 1.90909;
}

@media screen and (min-width: 768px) {
  .pre-title {
    font-size: 11px;
    line-height: 1.36364;
  }
}

.pre-title--light-theme {
  color: rgba(157, 164, 189, .6);
}

.pre-title--dark-theme {
  color: var(--first-text-color);
}

.pre-title:before {
  content: "";
  width: 60px;
  height: 1px;
  background-color: currentColor;
  margin-right: 20px;
}

.section-title {
  letter-spacing: .05em;
  font-family: Raleway, sans-serif;
  font-size: 25px;
  font-weight: 700;
  line-height: 1.4;
}

.section-title-accent {
  color: var(--accent-color);
}

.btn {
  letter-spacing: .09em;
  text-transform: uppercase;
  min-width: 160px;
  height: 40px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #ff6c00;
  border-radius: 25px;
  font-family: inherit;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.33;
  transition: color .3s linear, background-color .3s linear;
}

.backdrop {
  z-index: 100;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, .5);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
}

.backdrop.visually-hidden {
  opacity: 0;
  pointer-events: none;
}

.backdrop.visually-hidden .modal {
  transform: translate(-50%, -50%)scale(.9);
}

.modal {
  width: calc(100% - 30px);
  min-height: 500px;
  transform: translate(-50%, -50%) rotate(0) scale(1);
  background-color: #fff;
  border-radius: 10px;
  padding: 40px;
  font-size: 5px;
  transition: transform .5s;
  position: absolute;
  top: 50%;
  left: 50%;
}

@media screen and (min-width: 480px) {
  .modal {
    width: 450px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 528px;
  }
}

.modal__title {
  color: var(--accent-color);
  text-align: center;
  margin-bottom: 20px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.3;
}

.modal-open {
  overflow: hidden;
}

.modal-close {
  width: 30px;
  height: 30px;
  fill: #000;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  margin: 0;
  padding: 0;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal-close__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-close:hover {
  fill: #2196f3;
  transform-origin: 50%;
  transform: rotate(180deg);
}

.modal-checkbox {
  letter-spacing: .03em;
  color: #2196f3;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  padding-bottom: 30px;
  font-size: 14px;
  line-height: 1.7;
  display: flex;
  position: relative;
}

.modal-checkbox__link {
  color: #2196f3;
  text-decoration-skip-ink: none;
  text-decoration: underline;
}

.modal-checkbox__link:hover {
  cursor: pointer;
}

.modal-checkbox__label {
  letter-spacing: .03em;
  color: #757575;
  font-size: 12px;
  line-height: 1.17;
}

.modal-checkbox__icon {
  width: 16px;
  height: 15px;
  min-height: 16px;
  min-width: 16px;
  border: 2px solid #000;
  border-radius: 2px;
  margin-right: 9px;
  transition: border .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.modal-checkbox--hidden:checked + .modal-checkbox__icon {
  background-color: #2196f3;
  background-image: url("icon check.040768ec.svg");
  background-size: contain;
  background-origin: border-box;
  border-color: #2196f3;
}

.modal-checkbox--hidden:focus ~ .modal-checkbox__icon, .modal-checkbox:hover .modal-checkbox__icon {
  border: 2px solid #2196f3;
}

.modal-checkbox--hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.modal-form__wrap {
  width: 100%;
  height: 40px;
  margin-bottom: 28px;
  display: block;
  position: relative;
}

.modal-form__wrap:nth-child(4) {
  margin-bottom: 0;
}

.modal-form__input {
  width: 100%;
  height: 100%;
  color: #757575;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: none;
  padding-left: 42px;
  font-size: 14px;
  line-height: 1.17;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-form .modal-comment {
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-form__input:focus, .modal-form .modal-comment:focus {
  border-color: var(--accent-color);
}

.modal-form__label {
  letter-spacing: .01em;
  color: #757575;
  letter-spacing: .01em;
  font-size: 12px;
  line-height: 1.17;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  top: 50%;
  left: 42px;
  transform: translateY(-50%);
}

.modal-form__wrap:focus-within > .modal-form__label, .modal-form__input:not(:placeholder-shown) + .modal-form__label {
  transform: translate(-42px, -3.1em);
}

.modal-form__icon {
  fill: var(--secondary-text-color);
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
}

.modal-form__wrap:focus-within > .modal-form__icon {
  fill: var(--accent-color);
}

.modal-form__wrap:focus-within > .modal-comment__input {
  border-color: var(--accent-color);
}

